import React from 'react';
import './styles/Home.css'; 
import headshotImage from './images/profile.png'; 
import { FaGithub, FaLinkedin, FaEnvelope } from 'react-icons/fa'; // Import social icons

const Home = () => {
  return (
    <div className="home">
      <header className="header">
        <h1>Welcome to My Portfolio</h1>
      </header>

      <section className="intro">
        <div className="intro-item">
          <img src={headshotImage} alt="Headshot" className="headshot" />
        </div>
        <div className="intro-item">
          <p>
            Hi, I'm Abdouaziz Jom, a junior studying Computer and Information Science at the University of Michigan...
          </p>
          <div className="social-links">
            <a href="https://github.com/azjom"><FaGithub /></a>
            <a href="mailto:abdou.jom16@gmail.com"><FaEnvelope /></a>
            <a href="https://linkedin.com/in/abdouazizjom"><FaLinkedin /></a>
          </div>
        </div>
      </section>

      <section className="skills">
        <h2>Skills</h2>
        <div className="skills-list">
          <ul>
            <li>Languages: C++, C#, C, Python...</li>
            <li>Frameworks: Git/Github, VSCode, React...</li>
          </ul>
        </div>
      </section>
    </div>
  );
};

export default Home;
